<template>

    <div class="mypanel">

        <div class="hd">
            销售统计

        </div>
        <div class="bd">
        <div class="search-options">
            <table cellspacing="0" width="100%">
                <tbody>
                <tr>
                    <td>

                        开始时间:
                        <el-date-picker
                            v-model="begdate"
                            type="date"
                            value-format="yyyy-MM-dd"
                            format="yyyy-MM-dd"
                            placeholder="开始日期">
                        </el-date-picker>
                        结束时间:
                        <el-date-picker
                            v-model="enddate"
                            value-format="yyyy-MM-dd"
                            format="yyyy-MM-dd"
                            type="date"
                            placeholder="结束日期">
                        </el-date-picker>

                        <el-button @click="search" icon="el-icon-search" type="danger">统计</el-button>


                    </td>
                </tr>
                </tbody>
            </table>

        </div>

        <table  width="100%" border="0" cellspacing="0"
               cellpadding="0" class="ui-record-table">
            <thead>
            <tr>
                <th><b>编号</b></th>
                <th><b>商品</b></th>
                <th><b>销售数量</b></th>
                <th><b>总金额(元)</b></th>
            </tr>
            </thead>
            <tbody>

            <tr v-if="listMap!=null&&listMap.length==0">
                <td colspan="10">没有相关信息</td>
            </tr>


            <tr v-for="(map,index) in listMap ">
                <td>
                    <div class="sequence">{{index+1}}</div>
                </td>


                <td>{{map.name}}</td>
                <td>
                    {{map.borrowcount}}份
                </td>
                <td>
                    {{map.totalfee}}元
                </td>

            </tr>


            </tbody>
        </table>


        <div id="divLbSale" style="height:250px;width: 100%"></div>

        <div id="divLineSale" style="height:350px;width: 100%"></div>
        </div>

    </div>

</template>

<script>
    import {VueUtil} from "../util/vueutil";
    import echartPlus from "../util/echarts/echart-plus"
    export default {
        name: "xiaoshoutongjitext",
        data() {
            return {
                begdate: new Date().format("yyyy-MM-dd"),
                enddate:new Date(new Date().setDate(new Date().getDate()+7)).format("yyyy-MM-dd"),
                hostHead: this.$app.contextPath,
                listMap: [],
            }

        },
        methods: {

            async search() {
                let url = "admin/shorder/saletongji";
                let param = {
                    begdate: this.begdate,
                    enddate: this.enddate
                };


                let {data: res} = await this.$http.post(url, param);
                if (res != null && res.stateCode < 0) {
                    this.$message.error(res.des);
                    return;
                }
                this.listMap = res.data;

                echartPlus.echartBar({
                    data:this.listMap,
                    dom:document.querySelector("#divLbSale"),
                    title:"销售金额统计",
                    nameField:"name",
                    valueField:"totalfee"
                });
                echartPlus.echartLine({
                    data:this.listMap,
                    dom:document.querySelector("#divLineSale"),
                    title:"销售次数统计",
                    nameField:"name",
                    valueField:"borrowcount"
                });

            }

        },
        created() {

        }
    }
</script>

<style scoped>

</style>
